body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Press Start 2P';
  src: url(/flappybird-react/static/media/PressStart2P-Regular.81c0aae9.ttf);
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: 'Press Start 2P', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu,
  Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif;;
  font-size: 16px;
}

.App {
  position: fixed;
  height: 720px;
  width: 1280px;
  border: 4px solid #000;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
}

.Bird {
  position: absolute;
  top: 20px;
  left: 70px;
  width: 50px;
  height: 50px;
  border-radius: 25%;
  background: linear-gradient(75deg, blueviolet, deeppink);
}


.Pipe {
  position: absolute;
  width: 50px;
  background-color: #ce372b;
  box-shadow: inset 0px 0px 4px 0px #8c2022;
}


.Score {
  background-color: rgba(211,211,211, 0.6);
  color: #000;
  font-size: 32px;
  padding: 8px;
  z-index: 2;
  position: absolute;
}

.Controls {
  background-color: rgba(211,211,211, 0.6);
  color: #000;
  font-size: 16px;
  padding: 8px;
  z-index: 2;
  position: absolute;
  top: 0px;
  right: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Key {
  border: 2px solid #000;
  padding: 2px;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}

